.content {
    margin-left: 202px;
    padding: 0;
    height: 100vh;
    animation: fadein 0.75s;
    transition: margin 500ms;
}
.sidebar {
    width: 300px;
    color: red;
    visibility: visible;
    transition: visibility 0s, opacity 0.5s linear;
    opacity: 1;
}

@media screen and (min-width: 701px) {
    .topnav {
        display: none;
    }
}

@media screen and (max-width: 700px) {
    .sidebar {
        visibility: hidden;
        position: fixed;
        transition: visibility 0s, opacity 0.5s linear;
        opacity: 0;
    }

    .content {
        transition: margin 500ms;

        margin-left: 0;
        margin-top:2.75rem;
    }

    .topnav {
        display: block;
    }
}
@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
